/* eslint-disable */
import { createRouter, createWebHistory } from 'vue-router'
import BagsView from '@/views/BagsView.vue'
import BagView from '@/views/BagView.vue'
import EventsView from '@/views/EventsView.vue'
import EventView from '@/views/EventView.vue'
import HomeView from '@/views/HomeView.vue'
import LoginView from '@/views/LoginView.vue'
import StationsView from '@/views/StationsView.vue'
import StationView from '@/views/StationView.vue'
import UsersView from '@/views/UsersView.vue'
import UserView from '@/views/UserView.vue'
import VolunteersView from '@/views/VolunteersView.vue'
import VolunteerView from '@/views/VolunteerView.vue'

const routes = [
  /**
   *   Home
   */
  {
    path: '/home',
    name: 'Home',
    component: HomeView,
    meta: {
      title: 'Home',
      info: {
        path: ['home'],
        name: ['主頁']
      }
    },
  },
  /**
   *   Login
   */
  {
    path: '/login',
    name: 'Login',
    component: LoginView,
    meta: {
      title: 'Login',
      info: {
        path: ['login'],
        name: ['登入']
      }
    }
  },
  /**
   *   Users
   */
  {
    path: '/users',
    name: 'Users',
    component: UsersView,
    meta: {
      title: 'Users',
      path: ['users']
    },
    children: [
      {
        path: ':id',
        name: 'User Details',
        component: UserView,
        meta: {
          title: 'User Details',
          path: ['users', 'user detail']
        },
      },
    ],
  },
  /**
   * Events
   */
  {
    path: '/events',
    name: 'Events',
    component: EventsView,
    meta: { title: 'Events' },
    children: [
      // {},
      {
        path: ':id',
        name: 'Event Details',
        component: EventView,
        meta: {
          title: 'Events',
          info: {
            path: [':id'],
            name: ['主頁']
          }
        },
      },
      /**
       *   Stations
       */
      {
        path: ':id/stations',
        name: 'Stations',
        component: StationsView,
        meta: { title: 'Stations' },
        children: [
          {
            path: ':id',
            name: 'Stations Details',
            component: StationView,
            meta: { title: 'Station Details' },
          },
          /**
           *   Volunteers
           */
          {
            path: ':id/volunteers',
            name: 'Volunteers',
            component: VolunteersView,
            meta: { title: 'Volunteers' },
            children: [
              {
                path: ':id',
                name: 'Volunteer Details',
                component: VolunteerView,
                meta: { title: 'Volunteer Details' },
              },
              {
                path: ':id/bags',
                name: 'Blags',
                component: BagsView,
                meta: { title: 'Bags' },
                children: [
                  {
                    path: ':id',
                    name: 'Bag Details',
                    component: BagView,
                    meta: { title: 'Bag Details' },
                  },
                ],
              },
            ],
          },
        ],
      },
    ],
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
